@mixin d {
  @media only screen and (max-width: "1120px") {
    @content; } }
@mixin t {
  @media only screen and (max-width: "1023px") {
    @content; } }
@mixin m {
  @media only screen and (max-width: "767px") {
    @content; } }
@mixin s {
  @media only screen and (max-width: "474px") {
    @content; } }

@include font(arialn, arialn);
@mixin f {
  font-family: 'arialn', serif; }

@include font(arialnb, arialnb);
@mixin fb {
  font-family: 'arialnb', serif; }

$black: #000;
$white: #fff;

body {
  min-width: 320px;
  @include f;
  font-size: 16px;
  line-height: 1.3;
  color: $black;
  &.no-scroll {
    overflow: hidden; } }

body,
button,
input,
textarea,
select {}

a {
  text-decoration: none; }

button {
  background: none; }

.wrapper {
  min-height: 100vh;
  background: #071539; }

.center {
  max-width: 1065px;
  margin: 0 auto;
  padding: 0 20px; }

.main {
  margin-bottom: 40px;
  background: url('../img/bg-main.jpg') no-repeat 50% 0 / auto 100%;
  @include m {
    overflow: hidden;
    margin: 0; }
  &__center {
    position: relative;
    min-height: 740px;
    padding: 65px 20px 55px;
    @include t {
      min-height: auto;
      padding: 25px 20px; }
    @include m {
      padding: 20px 15px 50px; } }
  &__control {
    @include t {
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 80px 30px 50px;
      background: #071539;
      z-index: 100;
      transform: translateY(-100%);
      opacity: 0;
      transition: all .4s; }
    @include m {
      padding: 40px 15px 20px; }
    &.visible {
      transform: translateY(0);
      opacity: 1; } }
  &__burger {
    display: none;
    position: absolute;
    top: 50px;
    right: 50px;
    width: 32px;
    height: 42px;
    background: url('../img/menu.png') no-repeat 0 0 / 32px 32px;
    z-index: 110;
    @include t {
      display: inline-block; }
    @include m {
      top: 31px;
      right: 20px;
      width: 24px;
      height: 24px;
      background-size: 24px 24px; }
    &.active {
      background-image: url('../img/close.png'); } }
  &__btns {
    position: absolute;
    display: flex;
    right: 0;
    left: 0;
    @include t {
      position: relative;
      flex-direction: column; } }
  &__btn {
    margin: 0 2px;
    @include t {
      width: 400px;
      margin-bottom: 30px; }
    @include m {
      width: 280px;
      margin-bottom: 15px; } }
  &__btn .btn__text {
    @include m {
      height: auto;
      padding: 0;
      background: none;
      font-size: 14px; } }
  &__btn:before,
  &__btn:after {
    @include m {
      display: none; } }
  &__btns_top {
    top: 15px;
    @include t {
      top: auto; } }
  &__btns_bot {
    bottom: 20px;
    @include t {
      bottom: auto; } }
  &__btns_top &__btn:nth-child(2) {
    flex: 1 0 auto;
    @include t {
      flex: 0 0 auto; } }
  &__btns_bot &__btn {
    flex: 1 0 auto;
    @include t {
      flex: 0 0 auto; } }
  &__btns_hide {
    display: none;
    @include t {
      display: flex; } }
  &__logo {
    position: relative;
    display: inline-block;
    margin-left: 40px;
    margin-bottom: 150px;
    font-size: 0;
    z-index: 4;
    @include t {
      margin-left: 0;
      margin-bottom: 240px; }
    @include m {
      margin-bottom: 200px; } }
  &__pic {
    width: 556px;
    @include t {
      width: 400px; }
    @include m {
      width: 220px; } }
  &__preview {
    position: absolute;
    top: 180px;
    left: 140px;
    font-size: 0;
    z-index: 2;
    @include t {
      top: 160px;
      left: 100px; }
    @include m {
      display: none; } }
  &__photo {
    width: 357px;
    @include t {
      width: 250px; } }
  &__head {
    position: absolute;
    top: 75px;
    right: 35px;
    font-size: 0;
    z-index: 2;
    @include d {
      right: 20px; }
    @include t {
      top: 110px; }
    @include m {
      top: 80px; } }
  &__img {
    max-width: 100%;
    @include t {
      width: 300px; }
    @include m {
      width: 160px; } }
  &__text {
    display: flex;
    align-items: center;
    max-width: 766px;
    height: 264px;
    padding-left: 45px;
    background: url('../img/bg-text.png') no-repeat 50% 50% / 100% 100%;
    text-align: center;
    @include fb;
    font-size: 21px;
    color: $white;
    @include m {
      max-width: 100%;
      height: auto;
      padding: 0;
      background: none;
      font-size: 16px; }
    br {
      @include m {
        display: none; } } }
  &__links {
    display: flex;
    justify-content: space-between;
    padding: 0 150px;
    @include t {
      padding: 0 50px; }
    @include m {
      padding: 0; }
    @include s {
      flex-direction: column; } }
  &__link:first-child {
    @include s {
      margin-bottom: 50px; } }
  &__link:first-child &__icon {
    padding-top: 10px; }
  &__link:first-child &__image {
    @include m {
      max-width: 75px; } }
  &__link:last-child &__image {
    @include m {
      max-width: 100px; } }
  &__link:hover &__info {
    color: darken($white, 20); }
  &__icon {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 202px;
    margin-bottom: 35px;
    @include m {
      min-height: auto;
      margin-bottom: 40px; }
    @include s {
      margin-bottom: 35px; }
    &:before {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      width: 193px;
      height: 193px;
      margin: -92px 0 0 -97px;
      border-radius: 50%;
      background-color: #183b7c;
      box-shadow: 0 0 58.5px 6.5px rgba(183, 210, 229, 0.92);
      opacity: 0.6;
      z-index: 1;
      @include m {
        width: 140px;
        height: 140px;
        margin: -67px 0 0 -70px;
        box-shadow: 0 0 28.5px 6.5px rgba(183, 210, 229, 0.92); } } }
  &__image {
    position: relative;
    max-width: 100%;
    z-index: 2; }
  &__info {
    text-align: center;
    text-shadow: 0 0 52.7px rgba(22, 48, 96, 0.99);
    @include fb;
    font-size: 21px;
    color: $white;
    transition: color .2s;
    @include m {
      font-size: 16px; } }
  &_other {
    margin-bottom: 0;
    padding-bottom: 20px;
    background: url('../img/bg-main-1.jpg') no-repeat 50% 120px / auto; }
  &_other &__center {
    padding-bottom: 0; }
  &_other &__preview {
    top: 0px;
    left: 65px;
    @include t {
      left: 20px; } }
  &_other &__preview &__photo {
    width: auto;
    @include d {
      width: 500px; }
    @include t {
      width: 400px; } }
  &_other &__logo {
    margin-bottom: 180px; }
  &_other &__text {
    position: relative;
    background: none;
    text-shadow: 0 0 52.7px rgba(22, 48, 96, 0.99);
    z-index: 10;
    @include m {
      margin-bottom: 50px; } }
  &_balance {
    background: url('../img/bg-main-1.jpg') no-repeat 50% 120px / auto;
    @include t {
      margin-bottom: 0; } }
  &_balance &__logo {
    margin-bottom: 0; }
  &_balance &__center {
    padding-bottom: 170px;
    @include t {
      padding-bottom: 100px; }
    @include m {
      padding-bottom: 0; } }
  &_harmony {
    margin-bottom: 0;
    @include t {
      background-size: auto; } }
  &_harmony &__logo {
    margin-bottom: 0; } }

.balance {
  margin-top: -20px;
  @include m {
    margin-top: 10px; }
  &__title {
    margin-bottom: 120px;
    text-align: center;
    @include fb;
    font-size: 42px;
    text-shadow: 0 0 84.8px rgba(7, 21, 57, 0.92);
    color: $white;
    @include m {
      margin-bottom: 40px;
      font-size: 24px; } }
  &__row {
    position: relative;
    display: flex;
    justify-content: space-between;
    @include m {
      flex-direction: column;
      align-items: center; } }
  &__item {
    text-align: center;
    @include t {
      flex: 0 0 250px;
      max-width: 250px; } }
  &__item:hover &__text {
    color: darken($white, 20); }
  &__text {
    position: relative;
    @include fb;
    font-size: 21px;
    text-shadow: 0 0 52.7px rgba(22, 48, 96, 0.99);
    color: $white;
    z-index: 2;
    transition: color .2s; }
  &__preview {
    margin-top: -70px;
    font-size: 0;
    @include t {
      display: flex;
      justify-content: center;
      align-items: center;
      min-height: 290px;
      margin-top: -40px; }
    @include m {
      min-height: auto;
      margin-top: -50px; } }
  &__man {
    position: absolute;
    top: -130px;
    left: 50%;
    transform: translateX(-50%);
    font-size: 0;
    z-index: 3;
    @include m {
      position: relative;
      top: auto;
      left: auto;
      transform: translateX(0);
      margin-top: -80px; } }
  &__pic {
    max-width: 100%; } }

.harmony {
  margin-top: -100px;
  @include t {
    margin-top: -40px; }
  @include m {
    margin-top: 0; }
  &__title {
    text-align: center;
    @include fb;
    font-size: 42px;
    text-shadow: 0 0 84.8px rgba(7, 21, 57, 0.92);
    color: $white;
    @include t {
      margin-bottom: 40px; }
    @include m {
      font-size: 26px; } }
  &__wrap {
    position: relative;
    height: 750px;
    @include t {
      height: 1200px; }
    @include m {
      display: flex;
      flex-direction: column;
      align-items: center;
      height: auto; } }
  &__item {
    position: absolute;
    text-align: center;
    @include m {
      position: relative;
      top: auto;
      left: auto;
      right: auto;
      bottom: auto; }
    &_1 {
      top: 130px;
      left: 35px; }
    &_2 {
      top: -30px;
      left: 200px; }
    &_3 {
      top: 107px;
      left: 50%;
      transform: translateX(-50%);
      @include t {
        top: 400px; }
      @include m {
        transform: translateX(0); } }
    &_4 {
      top: 10px;
      right: 160px; }
    &_5 {
      right: 25px;
      top: 200px; }
    &_6 {
      left: 140px;
      bottom: 70px; }
    &_7 {
      left: 50%;
      bottom: 30px;
      margin-left: -155px;
      @include t {
        left: 40px;
        bottom: 300px;
        margin: 0; } }
    &_8 {
      right: 50%;
      bottom: 30px;
      margin-right: -155px;
      @include t {
        right: 30px;
        bottom: 300px;
        margin: 0; } }
    &_9 {
      right: 150px;
      bottom: 90px; }
    &:not(:last-child) {
      @include m {
        margin-bottom: 40px; } } }
  &__item_7 &__preview,
  &__item_8 &__preview {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 136px;
    margin-bottom: 15px; }
  &__item:hover &__text {
    color: darken($white, 20); }
  &__preview {
    margin-bottom: 10px;
    font-size: 0; }
  &__pic {
    max-width: 100%; }
  &__text {
    @include fb;
    font-size: 21px;
    color: $white;
    transition: color .2s;
    @include m {
      font-size: 18px; } } }

.btn {
  position: relative;
  padding: 0 35px;
  @include fb;
  font-size: 17px;
  text-transform: uppercase;
  color: $white;
  transition: color .3s;
  &__text {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 32px;
    padding-bottom: 2px;
    background: url('../img/bg-btn.png') no-repeat 50% 50% / 100% 100%; }
  &:before,
  &:after {
    content: "";
    position: absolute;
    top: 0;
    width: 35px;
    height: 32px;
    background-repeat: no-repeat;
    background-position: 0 0;
    background-size: 35px 32px; }
  &:before {
    left: 0;
    background-image: url('../img/bg-btn-left.png'); }
  &:after {
    right: 0;
    background-image: url('../img/bg-btn-right.png'); }
  &:hover {
    color: #1381d6; } }

.content {
  margin-bottom: 60px;
  @include m {
    margin-bottom: 40px; }
  p {
    font-size: 16px;
    color: $white;
    @include m {
      font-size: 14px; }
    &:not(:last-child) {
      margin-bottom: 15px; } } }

.footer {
  &__wrap {
    padding: 20px 0;
    border-top: 1px solid rgba($white, .44); }
  &__copyright {
    color: $white; } }
